import axios from 'axios'
import router from '../router'
import * as user from './src/user'
import * as servers from './src/servers'
import * as travels from './src/travels'

export const UserApi = user
export const ServersApi = servers
export const TravelsApi = travels
export { CarouselApi } from './src/carousel'

// requesttokentokenheader
axios.interceptors.request.use(
  config => {
    const token = window.localStorage.getItem('token')
    if (token) { // tokenhttp headertoken
      config.headers.Authorization = `token ${token}`
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)

// response
axios.interceptors.response.use((response) => {
  const data = response.data

  // code
  switch (data.code) {
    case 1:
      // return
      return data.data

    default:
  }
  // code
  const err = new Error(data.message)

  err.data = data
  err.response = response

  throw err
}, (err) => { // 200
  const result = {}
  if (err && err.response) {
    switch (err.response.data.code) {
      case -1:
        result.message = ''
        break

      case -2:
        result.message = ''
        break

      case -4:
        result.message = ''
        break

      case -5:
        result.message = ''
        break

      case -6:
        result.message = ''
        break

      case -7:
        result.message = 'token'
        // 
        window.localStorage.removeItem('token')
        router.replace({
          path: '/login'
        })
        break

      case -8:
        result.message = 'bot,bot'
        break

      case -9:
        result.message = ''
        break

      case -10:
        result.message = ''
        break

      case -11:
        result.message = ''
        break

      case -12:
        result.message = ''
        break

      case -13:
        result.message = ''
        break

      case -14:
        result.message = '.'
        break

      case -15:
        result.message = ''
        break

      case -4000:
        result.message = ''
        break

      default: result.message = 'Interval Server Error'
    }
  }
  return Promise.reject(result)
})



